button {
  border: none !important;
  outline: none !important;
}
.workshop-slider-container {
  width: 100%;
  padding: 100px 0;
}
.slider-1,
.slider-2 {
  padding: 10px !important;
  margin: 20px 0;
  overflow-x: hidden;
}
.slider-1 {
  /* width: 60% !important; */
  margin: auto;
  position: relative !important;
}
.ws-icons-slider {
  width: 50%;
  margin: auto;
  position: relative;
}
.icon-slide {
  width: fit-content !important;
  min-width: 150px;
  display: flex !important;
  justify-content: center;
  align-items: center !important;
  padding: 10px 20px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}
.icon-slide:hover {
  background: #09bfad33;
  border-radius: 50px;
}
.icon-slide p {
  margin: 0 5px !important;
  font-size: 0.9rem;
}
.icon-slide img {
  width: 25px;
}
.workshop-slide {
  display: flex !important;
  justify-content: space-between;
  align-items: center;
  width: 60% !important;
  margin: auto;
  box-shadow: 2px 2px 10px -1px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  overflow: hidden;
}
.ws-image {
  width: 50%;
  background: #e03268;
  border-radius: 20px 0 0 20px;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  /* height: 100%; */
  height: 500px;
}
.ws-image img {
  width: 80%;
  min-width: 300px;
}
.ws-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 50%;
  padding: 10px 50px;
  height: 500px;
}
.ws-details .points {
  margin: 30px 0;
}
.ws-details .heading h1 {
  font-family: "Montserrat", sans-serif;
  font-size: 2.2rem;
  font-weight: 800 !important;
  text-align: left;
  color: rgba(0, 0, 0, 0.8);
}
.underline {
  height: 2px;
  background: #e03268;
  width: 60%;
}
.ws-details .points li {
  margin: 15px 0;
  width: fit-content;
  color: rgba(0, 0, 0, 0.5);
  list-style: none;
}
.ws-details .points li::before {
  content: "â€¢";
  font-weight: 800;
  color: #e03268;
  display: inline-block;
  width: 1em;
  /* margin-left: 0em; */
  transform: scale(3);
}
.ws-details .enroll-btn {
  background: white !important;
  padding: 0 !important;
  margin-left: 20px;
  overflow: hidden;
  box-shadow: 2px 2px 5px #e032693a;
  text-transform: capitalize !important;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.6);
  border-radius: 10px;
}
.ws-details .enroll-btn:hover {
  box-shadow: 2px 2px 15px #e032693a;
}
.ws-details .enroll-btn span > span {
  padding: 15px 20px;
}
.ws-details .enroll-btn .offer-text {
  background: #e03268 !important;
  color: white;
}
.custom-bg .slick-current .icon-slide {
  background: #09bfad !important;
  border-radius: 50px;
  color: white;
  font-weight: 600 !important;
  box-shadow: 2px 2px 10px #09bfadad !important;
}
.ws-prev-icon img,
.ws-next-icon img {
  width: 15px;
  opacity: 0.4;
}

.ws-prev-icon,
.ws-next-icon {
  position: absolute !important;
  left: -25px;
  top: 50%;
  z-index: 1;
  transform: translate(-10%, -50%);
  outline: none !important;
  border: none !important;
}
.ws-next-icon {
  left: auto;
  right: -25px;
}
.ws-next-icon img {
  transform: rotate(180deg);
}
.icon-slide-container,
.workshop-slide-container {
  padding: 10px !important;
}

@media screen and (max-width: 1550px) {
  .workshop-slide {
    width: 70% !important;
  }
  .ws-icons-slider {
    width: 60% !important;
  }
}

@media screen and (max-width: 1300px) {
  .workshop-slide {
    width: 80% !important;
  }
  .ws-icons-slider {
    width: 70% !important;
  }
  .ws-details .heading h1 {
    font-size: 2rem;
  }
  .ws-details .points li {
    font-size: 0.9rem;
  }
}

@media screen and (max-width: 1150px) {
  .workshop-slide {
    width: 90% !important;
  }
  .ws-icons-slider {
    width: 80% !important;
  }
}

@media screen and (max-width: 900px) {
  .workshop-slide .ws-image {
    display: none;
  }
  .workshop-slide .ws-details {
    width: 100%;
    justify-content: center !important;
  }
}

@media screen and (max-width: 490px) {
  .workshop-slide {
    width: 100% !important;
  }
  .ws-details {
    padding: 10px 20px;
  }
  .workshop-slide .ws-details h1 {
    font-size: 1.5rem;
  }
  .workshop-slide .ws-details .points {
    margin: 10px 0;
  }

  .workshop-slide .enroll-btn span > span {
    padding: 15px 20px !important;
  }
}
@media screen and (max-width: 453px) {
  .custom-bg .slick-track {
    width: 3500px !important;
  }
}
.winners-slider-2 {
  padding: 0 !important;
}
