}
.a {
  position: relative;
  top: 2px;
  right: 1%;
  text-align: center;
  color: white;
  float: right;
  height: 80px;
  width: 70px;
}
.tag {
  position: absolute;
  top: -7px;
  right: 2%;
  z-index: 0;
  float: right;
  overflow: hidden;
}
.discount {
  color: white;
  text-align: center;
  position: relative;
  padding-right: 6px;
  padding-left: 6px;
  font-size: 20px;
  font-family: Montserrat-Regular;
  line-break: anywhere;
}
.banner-image {
  background-position: 0 10% !important;
}
.status-badge {
  float: right;
  background: #06bfac31;
  padding: 2px 5px;
  font-size: 0.6rem;
  border-radius: 2px;
  color: #06bfad;
  font-family: "Montserrat", sans-serif;
  font-weight: 800;
  text-transform: uppercase;
  line-height: normal !important;
}
.coming-soon {
  width: 100%;
  border-radius: 0 !important;
}
.main-course {
  transform: scale(1.15);
  box-shadow: 2px 2px 60px #06bfac44 !important;
  background: white !important;
}
.secondary-course {
  transform: scale(0.95);
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1) !important;
}

@media screen and (max-width: 992px) {
  .main-course {
    transform: scale(1);
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1) !important;
  }
  .secondary-course {
    transform: scale(1);
  }
}
.product-card {
  transition: all 0.2s ease-in-out;
}
.product-card:hover {
  transform: scale(1.01);
}

.home {
  display: grid;
  place-items: center;
  height: 100vh;
  min-height: 800px;
  background: #06bfad;
}
.section-container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  overflow: hidden !important;
  position: relative;
  font-family: "Montserrat", sans-serif !important;
}
.section-half {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 5%;
  color: white;
}
.with-shadow {
  box-shadow: 5px 2px 10px rgba(0, 0, 0, 0.1) !important;
  z-index: 1;
}
.robo-image {
  position: absolute;
  top: 50%;
  left: 54%;
  max-width: 50%;
  transform: translate3d(-54%, -50%, 0);
  z-index: 2;
  -webkit-filter: drop-shadow(2px 2px 8px rgba(0, 0, 0, 0.2));
          filter: drop-shadow(2px 2px 8px rgba(0, 0, 0, 0.2));
  animation: float infinite ease-in-out 1.2s alternate-reverse;
  transition: all 0.2s ease-in-out;
}
.robo-image:hover {
  -webkit-filter: drop-shadow(4px 4px 8px rgba(0, 0, 0, 0.35));
          filter: drop-shadow(4px 4px 8px rgba(0, 0, 0, 0.35));
}
.spin-animation {
  animation: spin 0.5s ease-in-out alternate-reverse;
}
.course-list {
  background: #0cb2a2;
  position: relative;
}
.course-list::before {
  content: " ";
  display: block;
  position: absolute;
  right: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  opacity: 0.03;
  background-image: url(/static/media/blackboard.63de7bec.webp);
  background-repeat: repeat;
  /* background-position: 50% 0; */
  background-blend-mode: screen;
  background-size: contain;
}
.section-half .details h1 {
  font-family: "Montserrat", sans-serif !important;
  text-transform: uppercase;
  font-weight: 900;
  font-size: 3.2rem;
  text-shadow: 2px 2px 5px #03776b77;
}
.section-half p {
  font-family: "Montserrat", sans-serif !important;
  font-weight: 400;
  margin: 2rem 0;
  font-size: 1.2rem;
  line-height: 30px !important;
  /* width: 60%; */
  word-wrap: break-word;
  text-shadow: 2px 2px 5px #028b7e77;
}
.section-half .btns .hero-btn {
  background: #e03268 !important;
  color: white;
  font-weight: 600;
  text-transform: capitalize;
  border-radius: 50px;
  padding: 12px 40px;
  font-size: 1rem;
  border: none !important;
  outline: none !important;
  box-shadow: none !important;
  transition: all 0.2s ease-in-out;
}

.section-half .btns .hero-btn:nth-child(2) {
  margin-left: 30px;
  background: white !important;
  color: black;
}
.section-half .btns .hero-btn:hover {
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2) !important;
}

/*Courses*/
.course-list {
  padding: 0 5%;
}
.course-container {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  align-self: flex-end;
  width: 60%;
}
.course-list h3 {
  align-self: flex-start;
  font-size: 1.1rem;
  position: relative;
}
.course-list h3::after {
  content: "";
  width: 60%;
  height: 3px;
  background: #e03268;
  position: absolute;
  bottom: -10px;
  left: 0;
  border-radius: 5px;
}
.course-row1,
.course-row2 {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 20px 0;
  width: 100%;
}
.course-row1 .course:nth-child(2),
.course-row2 .course:nth-child(2) {
  margin-left: 20px;
}
.course-container .course {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 150px;
  width: 150px;
  border-radius: 20px;
  /* background: #06bfad; */
  border: 4px solid #06bfad;
  box-shadow: inset 2px 2px 5px rgba(0, 0, 0, 0.1),
    2px 2px 5px rgba(0, 0, 0, 0.1);
  cursor: pointer !important;
}
.course-container .course img {
  width: 100px;
  position: relative;
  -webkit-filter: drop-shadow(2px 2px 5px rgba(0, 0, 0, 0.2));
          filter: drop-shadow(2px 2px 5px rgba(0, 0, 0, 0.2));
  transition: all 0.2s ease-in-out;
}
.course-container .course:hover img {
  width: 104px;
}
.course-container .course p {
  margin: auto;
  font-weight: 600;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
  cursor: pointer !important;
}

.robo-image-tab,
.robo-image-mobile {
