/*External CSS*/
.outer-container {
  background-color: #ecb739;
  background: linear-gradient(
    135deg,
    /* #0fa3e5,
    #018dc0, */ #fdf25f,
    #ffb609,
    #f08cb2,
    #ea679a
  );
  padding: 50px 0;
  margin: 20px auto;
  border-radius: 20px;
  overflow: hidden;
  /* clip-path: polygon(0 0, 100% 8%, 100% 100%, 0 92%); */
  position: relative;
  width: var(--fixedWidth);
  /* transition: all 2s ease-in-out; */
  /* animation-timing-function: ease; */
  background-size: 400% 400%;
  animation: rotate-gradient 10s ease infinite;
}
.outer-container .vertical-text {
  position: absolute;
  left: 0;
  top: 50%;
  transform: rotate(-90deg) translate3d(0, -50%, 0);
  transform-origin: center;
  text-transform: uppercase;
  letter-spacing: 20px;
  color: white;
  font-weight: 800;
}
.outer-container .vertical-text:nth-child(2) {
  left: auto;
  right: 0;
  transform: rotate(90deg) translate3d(0, -50%, 0);
}
.outer-container > div[aria-label~="animation"] {
  position: absolute !important;
  width: 100% !important;
  height: 100% !important;
  pointer-events: none !important;
  z-index: -1 !important;
}
@keyframes rotate-gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
