@media only screen and (max-width: 1279px) {
  .robo-image {
    display: none;
  }
  .home .robo-section {
    min-height: 700px;
    padding-bottom: 0;
  }
  .course-list {
    justify-content: center !important;
  }
  .robo-image-mobile {
    display: none;
  }
  .robo-image-tab {
    display: block;
    max-width: 35%;
    /* animation: float-tab infinite ease-in-out 1.2s alternate-reverse; */
  }
  .course-container {
    flex-direction: row;
    align-items: center;
    justify-content: space-between !important;
    width: fit-content;
    align-self: center;
    position: relative;
  }
  .course-row1,
  .course-row2 {
    width: 50%;
    justify-content: space-around;
    margin-top: 30px;
  }
  .course-row2 {
    margin-left: 30px;
  }
  .course-container h3 {
    width: fit-content !important;
    position: absolute;
    left: 0;
    top: 0;
  }
}
