@keyframes float {
  from {
    top: 48%;
    transform: translate3d(-54%, -48%, 0);
  }
  to {
    top: 52%;
    transform: translate3d(-54%, -52%, 0);
  }
}

@keyframes float-tab {
  from {
    margin-top: -10px;
  }
  to {
    margin-top: 10px;
  }
}
