.carousel-control-prev-icon {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='%23000' width='15' height='15' viewBox='0 0 8 8'%3e%3cpath d='M5.25 0l-4 4 4 4 1.5-1.5L4.25 4l2.5-2.5L5.25 0z'/%3e%3c/svg%3e") !important;
}
.carousel-control-next-icon {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='%23000' width='15' height='15' viewBox='0 0 8 8'%3e%3cpath d='M2.75 0l-1.5 1.5L3.75 4l-2.5 2.5L2.75 8l4-4-4-4z'/%3e%3c/svg%3e") !important;
}
.carousel-control-next,
.carousel-control-prev {
  width: 5% !important;
}
.carousel-control-next-icon,
.carousel-control-prev-icon {
  background-color: #fff;
  opacity: 1;
  border-radius: 25px;
  box-shadow: 1px 1px 1px #000;
  background-repeat: no-repeat !important;
  background-size: 45%;
  height: 40px !important;
  width: 40px !important;
}
.Comment {
  font-family: Montserrat-Regular;
  opacity: 0.7;
}
.carousel {
  margin-top: 15%;
}
.image1 {
  height: 250px;
  width: auto;
  padding-left: 35%;
  margin-bottom: 40%;
}
.image2 {
  position: absolute;
  top: 55px;
  left: 85px;
  height: 250px;
  width: 250px;
  z-index: 1;
  border-radius: 20px;
}
.float-right {
  padding-right: 10%;
  opacity: 0.5;
  font-family: Montserrat-Regular;
}
.subscribers {
  display: block;
  border: 1px solid #e03268;
  border-radius: 20px;
  padding: 27px;
  box-shadow: 0 0 0.4px #e03268;
}
.subscribers h1 {
  font-size: 55px;
  color: #e03268;
  padding-top: 7px;
}
.subscribers h1 strong {
  text-shadow: 1px 1px 3px #e03268;
}
.carousel-indicators li {
  display: none;
}

/*External CSS*/
.outer-container {
  background-color: #ecb739;
  background: linear-gradient(
    135deg,
    /* #0fa3e5,
    #018dc0, */ #fdf25f,
    #ffb609,
    #f08cb2,
    #ea679a
  );
  padding: 50px 0;
  margin: 20px auto;
  border-radius: 20px;
  overflow: hidden;
  /* clip-path: polygon(0 0, 100% 8%, 100% 100%, 0 92%); */
  position: relative;
  width: var(--fixedWidth);
  /* transition: all 2s ease-in-out; */
  /* animation-timing-function: ease; */
  background-size: 400% 400%;
  -webkit-animation: rotate-gradient 10s ease infinite;
  -moz-animation: rotate-gradient 10s ease infinite;
  animation: rotate-gradient 10s ease infinite;
}
.outer-container .vertical-text {
  position: absolute;
  left: 0;
  top: 50%;
  transform: rotate(-90deg) translate3d(0, -50%, 0);
  transform-origin: center;
  text-transform: uppercase;
  letter-spacing: 20px;
  color: white;
  font-weight: 800;
}
.outer-container .vertical-text:nth-child(2) {
  left: auto;
  right: 0;
  transform: rotate(90deg) translate3d(0, -50%, 0);
}
.outer-container > div[aria-label~="animation"] {
  position: absolute !important;
  width: 100% !important;
  height: 100% !important;
  pointer-events: none !important;
  z-index: -1 !important;
}
@keyframes rotate-gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@media (max-width: 800px) {
  .subscribers {
    padding: 16px;
  }
  .subscribers h1 {
    font-size: 36px;
  }
  .subscribers h1 strong {
    text-shadow: 1px 1px 2px #e03268;
  }
}
@media (max-width: 768px) {
  .image1 {
    height: 200px;
  }
  .image2 {
    position: absolute;
    left: 35px;
    top: 40px;
  }
  .Comment {
    font-size: 26px !important;
  }
  .float-right {
    font-size: 12px !important;
  }
}
@media (max-width: 600px) {
  .outer-container {
    padding: 0;
  }
}

@media (max-width: 450px) {
  .subscribers {
    padding: 10px;
  }
  .subscribers h1 {
    font-size: 18px;
  }
  .subscribers h1 strong {
    text-shadow: 1px 1px 1px #e03268;
  }
  /* .custom-iframe {
    height: 130px !important;
  } */
}
