.courses {
  margin: 20px auto;
}

@media (max-width: 768px) {
  .courses {
    margin: 0 auto;
  }
}
.courses h4 {
  font-family: Montserrat-Bold;
}
.MuiPaper-rounded {
  border-radius: 10px;
}
.MuiListItemIcon-root {
  min-width: 35px !important;
  color: unset !important;
}
.MuiDivider-root {
  background-color: #06bfad !important;
}
.watch,
.prize {
  font-family: Montserrat-Bold;
  font-size: 15px !important;
  color: #06bfad !important;
  font-weight: bold !important;
}
.prize del {
  font-size: 10px !important;
  font-family: "Montserrat-Regular";
}
.MuiCardActions-root {
  /* background-image: url("../../public/icons/btnwave.svg"); */
  background-repeat: no-repeat;
  background-size: cover;
  padding: 0 !important;
  margin-left: 0 !important;
  margin-right: 0 !important;
  border-top: none !important;
}
.MuiCardActions-root:hover Button {
  border: none !important;
  outline: none !important;
}
.watch {
  font-family: Montserrat-Bold;
  font-size: 15px !important;
  color: #fff !important;
  font-weight: bold !important;
  background: #06bfad !important;
  padding: 10px 5px !important;
  border-radius: 10px 0 0 0 !important;
}
.a {
  position: relative;
  top: 2px;
  right: 1%;
  text-align: center;
  color: white;
  float: right;
  height: 80px;
  width: 70px;
}
.tag {
  position: absolute;
  top: -7px;
  right: 2%;
  z-index: 0;
  float: right;
  overflow: hidden;
}
.discount {
  color: white;
  text-align: center;
  position: relative;
  padding-right: 6px;
  padding-left: 6px;
  font-size: 20px;
  font-family: Montserrat-Regular;
  line-break: anywhere;
}
.banner-image {
  background-position: 0 10% !important;
}
.status-badge {
  float: right;
  background: #06bfac31;
  padding: 2px 5px;
  font-size: 0.6rem;
  border-radius: 2px;
  color: #06bfad;
  font-family: "Montserrat", sans-serif;
  font-weight: 800;
  text-transform: uppercase;
  line-height: normal !important;
}
.coming-soon {
  width: 100%;
  border-radius: 0 !important;
}
.main-course {
  transform: scale(1.15);
  box-shadow: 2px 2px 60px #06bfac44 !important;
  background: white !important;
}
.secondary-course {
  transform: scale(0.95);
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1) !important;
}

@media screen and (max-width: 992px) {
  .main-course {
    transform: scale(1);
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1) !important;
  }
  .secondary-course {
    transform: scale(1);
  }
}
.product-card {
  transition: all 0.2s ease-in-out;
}
.product-card:hover {
  transform: scale(1.01);
}
